import "./index.scss"

wp.blocks.registerBlockType('elrplugin/element-listy-relacje', {
    title: "Element Listy Realcje",
    icon: "editor-ol",
    category: "common",
    attributes: {
        text: { type: "string" },
        link: { type: "string" }
    },
    description: 'Dodaj element do listy na podstronie "Relacje"',
    example: {
        attributes: {
            text: "Tutaj wpisz tekst",
            link: "#"
        }
    },
    edit: EditComponent,
    save: (props) => {
        return null
    }
})

function EditComponent (props) {

    return (
        <div className="account-list-element-edit-block">
            <p className="account-list-element-headline">Generator elementu listy Relacji</p>
            <input type="text" name="theText" onChange={e => props.setAttributes({ text: e.target.value })} placeholder="Tutaj wpisz to, co ma być wyświetlane na stronie." />
            <input type="text" name="thelink" onChange={e => props.setAttributes({ link: e.target.value })} placeholder="Tutaj wklej link do odpowiedniego pliku." />
        </div>
    )
}